	export default {
		data() {
			return {
				transform: 'translateY(50vh)',
				timer: 0,
				backgroundColor: 'rgba(0,0,0,0)',
				show: false,
				config: {},
			};
		},
		props:{
			contentHeight:{
				type: Number,
				default: 0
			},
			//是否是tabbar页面
			hasTabbar:{
				type: Boolean,
				default: false
			},
			shareList:{
				type: Array,
				default: function(){
					return [];
				}
			}
		},
		created() {
			const height = uni.upx2px(this.contentHeight) + 'px';
			this.config = {
				height: height,
				transform: `translateY(${height})`,
				backgroundColor: 'rgba(0,0,0,.4)',
			}
			this.transform = this.config.transform;
		},
		methods:{
			toggleMask(){
				//防止高频点击
				if(this.timer == 1){
					return;
				}
				this.timer = 1;
				setTimeout(()=>{
					this.timer = 0;
				}, 500)
				
				if(this.show){
					this.transform = this.config.transform;
					this.backgroundColor = 'rgba(0,0,0,0)';
					setTimeout(()=>{
						this.show = false;
						this.hasTabbar && uni.showTabBar();
					}, 200)
					return;
				}
				
				this.show = true;
				//等待mask重绘完成执行
				if(this.hasTabbar){
					uni.hideTabBar({
						success: () => {
							setTimeout(()=>{
								this.backgroundColor = this.config.backgroundColor;
								this.transform = 'translateY(0px)';
							}, 10)
						}
					});
				}else{
					setTimeout(()=>{
						this.backgroundColor = this.config.backgroundColor;
						this.transform = 'translateY(0px)';
					}, 10)
				}
			},
			//防止冒泡和滚动穿透
			stopPrevent(){},
			//分享操作
			shareToFriend(text){
				if(text == "微信")
				{		
					uni.share({
						provider: "weixin",
						scene: "WXSceneSession",
						type: 1,
						summary: "我正在使用萌二app，能拍视频看视频，还能购买各种东西哟~ 赶紧跟我一起来体验！",
						success: function (res) {
							console.log("success:" + JSON.stringify(res));
							uni.showToast({
								title:"分享成功",
								icon:"success"
							})
						},
						fail: function (err) {
							console.log("fail:" + JSON.stringify(err));
							uni.showToast({
								title:"分享成功",
								icon:"none"
							})
						}
					})
				}
				if(text=="朋友圈")
				{
					uni.share({
					    provider: "weixin",
					    scene: "WXSenceTimeline",
					    type: 1,
					    summary: "我正在使用萌二app，能拍视频看视频，还能购买各种东西哟~ 赶紧跟我一起来体验！",
					    success: function (res) {
					        console.log("success:" + JSON.stringify(res));
					    },
					    fail: function (err) {
					        console.log("fail:" + JSON.stringify(err));
					    }
					});
				}
				if(text=="QQ")
				{
					{
						uni.share({
						    provider: "qq",
						    scene: "WXSceneSession",
						    type: 1,
						    summary: "我正在使用萌二app，能拍视频看视频，还能购买各种东西哟~ 赶紧跟我一起来体验！",
						    success: function (res) {
						        console.log("success:" + JSON.stringify(res));
						    },
						    fail: function (err) {
						        console.log("fail:" + JSON.stringify(err));
						    }
						});
					}
				}
				if(text=="新浪")
				{
					{
						uni.share({
						    provider: "sinaweibo",
						    scene: "WXSceneSession",
						    type: 1,
						    summary: "我正在使用萌二app，能拍视频看视频，还能购买各种东西哟~ 赶紧跟我一起来体验！",
						    success: function (res) {
						        console.log("success:" + JSON.stringify(res));
						    },
						    fail: function (err) {
						        console.log("fail:" + JSON.stringify(err));
						    }
						});
					}
				}
				if(text=="新浪")
					{
						{
							uni.share({
							    provider: "sinaweibo",
							    scene: "WXSenceTimeline",
							    type: 1,
							    summary: "我正在使用萌二app，能拍视频看视频，还能购买各种东西哟~ 赶紧跟我一起来体验！",
							    success: function (res) {
							        console.log("success:" + JSON.stringify(res));
							    },
							    fail: function (err) {
							        console.log("fail:" + JSON.stringify(err));
							    }
							});
						}
				}
		}
	},
}
